<template>
    <div class="addDetail">
        <header>
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>搜索地址</span>
        </header>
        <section class="A1">
            <div class="A11"><input type="text" placeholder="请输入小区/写字楼/学校等" v-model="keyword"><span @click="addDe">确认</span></div>
            <div class="A12">为了满足商家的送餐要求，建议您从列表中选择地址</div>
            <div class="A13" v-if="isShow">
                <p>找不到地址？</p>
                <p>请尝试输入小区、写字楼或学校名</p>
                <p>详细地址（如门牌号）可稍后输入哦。</p>
            </div>
        </section>
        <section class="A2">
            <ul>
                <li v-for="c in city"  @click="toLast(c)">
                    <p>{{c.name}}</p>
                    <p>{{c.address}}</p>
                </li>
            </ul>
        </section>
    </div>
</template>

<script>
    export default {
        name: "AddDetail",
        data(){
            return{
                city_id:"",
                keyword:"",
                city:[],
                isShow:true,
            }
        },
        created(){
            this.city_id = localStorage.getItem("city_id");
            this.keyword = this.$route.query.name;
        },
        methods:{
            addDe(){
                this.axios.get("/v1/pois?city_id="+this.city_id+"&keyword="+this.keyword+"&type=search").then(res=>{
                    this.city = res;
                    console.log(res);
                    if(this.city == ""){
                        this.isShow = true;
                    }else{
                        this.isShow = false;
                    }
                }).catch(err=>{});
            },
            toLast(c){
               this.$router.go(-1);
               localStorage.setItem("address",JSON.stringify(c));
            }
        }
    }
</script>

<style scoped>
    .addDetail{
        width: 100%;
        padding-top: 1.95rem;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    .A11{
        display: flex;
        justify-content: space-between;
        background: #fff;
        padding: .5rem;
    }
    .A11>input{
        display: block;
        width: 10.8rem;
        padding: .4rem;
        background: #f2f2f2;
        border: 1px solid #ddd;
        border-radius: 5px;
        font-size: .6rem;
        outline: none;
    }
    .A11>span{
        display: block;
        width: 3rem;
        text-align: center;
        background: #3199e8;
        font-size: .7rem;
        color: #fff;
        border-radius: 5px;
        line-height: 1.5rem;
    }
    .A12{
        background: #fff6e4;
        font-size: .62rem;
        color: #ff883f;
        text-align: center;
        padding: .2rem 0;
    }
    .A13{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 100%;
    }
    .A13>p{
        width: 100%;
        text-align: center;
        font-size: .7rem;
        color: #969696;
        margin-bottom: .4rem;
    }
    .A2{
        background: #f2f2f2;
    }
    .A2 li{
        border-bottom: 1px solid #ccc;
        padding: .4rem;
    }
    .A2 p:nth-child(1){
        margin-bottom: .2rem;
        font-size: .65rem;
        color: #969696;
        line-height: 0.8rem;
    }
    .A2 p:nth-child(2){
        font-size: .65rem;
        color: #969696;
        line-height: 0.8rem;
    }
</style>